import React, { useRef, useState, forwardRef, useImperativeHandle, useEffect } from 'react';
import { PaperClipIcon } from '@heroicons/react/24/outline';
import * as pdfjsLib from 'pdfjs-dist';

// PDF.js worker  -    
const initPdfWorker = () => {
  if (typeof window !== 'undefined' && pdfjsLib) {
    // CloudFront URL    
    const workerUrl = window.location.hostname === 'localhost'
      ? '/pdf.worker.min.js'
      : `${window.location.origin}/pdf.worker.min.js`;

    pdfjsLib.GlobalWorkerOptions.workerSrc = workerUrl;
    console.log('PDF.js Worker initialized:', workerUrl);
  }
};

const FileUploadButton = forwardRef(({ onFileContent, disabled }, ref) => {
  const fileInputRef = useRef(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [uploadedFiles, setUploadedFiles] = useState([]);

  //    PDF worker 
  useEffect(() => {
    initPdfWorker();
  }, []);

  //    
  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return Math.round(bytes / Math.pow(k, i) * 100) / 100 + sizes[i];
  };

  //   
  const getFileExtension = (fileName) => {
    const parts = fileName.split('.');
    return parts.length > 1 ? parts.pop().toUpperCase() : 'FILE';
  };

  //    
  const processFile = async (file) => {
    if (!file) return;

    //    
    if (uploadedFiles.some(f => f.name === file.name && f.size === file.size)) {
      alert('  .');
      return;
    }

    setIsProcessing(true);

    try {
      //    (500MB  )
      if (file.size > 500 * 1024 * 1024) {
        alert('  500MB   .');
        return;
      }

      let fileContent = '';
      let fileType = '';
      let pageCount = null;

      //   
      if (file.type === 'text/plain' || file.name.endsWith('.txt')) {
        //   
        fileContent = await file.text();
        fileType = 'text';
      } else if (file.type === 'application/pdf' || file.name.endsWith('.pdf')) {
        // PDF  
        const result = await handlePdfFile(file);
        if (!result) return;
        fileContent = result.text;
        fileType = 'pdf';
        pageCount = result.pageCount;
      } else {
        alert('   . txt, pdf   .');
        return;
      }

      //   
      const fileInfo = {
        id: Date.now() + Math.random(),
        name: file.name,
        size: file.size,
        sizeFormatted: formatFileSize(file.size),
        type: fileType,
        extension: getFileExtension(file.name),
        content: fileContent,
        pageCount,
        uploadedAt: new Date()
      };

      //   
      setUploadedFiles(prev => [...prev, fileInfo]);

      //  
      onFileContent(fileContent, {
        fileName: file.name,
        fileType,
        fileSize: file.size,
        pageCount
      });
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    } finally {
      setIsProcessing(false);
    }
  };

  const handleFileSelect = async (event) => {
    const files = Array.from(event.target.files);
    
    //    
    for (const file of files) {
      await processFile(file);
    }
    
    //     
    event.target.value = '';
  };


  //      
  useImperativeHandle(ref, () => ({
    handleFile: processFile
  }));

  const handlePdfFile = async (file) => {
    try {
      console.log('PDF   :', file.name, 'Size:', file.size);
      //   PDF.js   
      const arrayBuffer = await file.arrayBuffer();
      console.log('ArrayBuffer  ');
      const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise;
      console.log('PDF   ,  :', pdf.numPages);
      
      let fullText = '';
      const numPages = pdf.numPages;
      
      //    
      for (let pageNum = 1; pageNum <= numPages; pageNum++) {
        const page = await pdf.getPage(pageNum);
        const textContent = await page.getTextContent();
        const pageText = textContent.items.map(item => item.str).join(' ');
        fullText += pageText + '\n\n';
      }
      
      //    
      if (!fullText.trim()) {
        alert('PDF    .   PDF  .');
        return null;
      }
      
      return {
        text: fullText.trim(),
        pageCount: numPages
      };
    } catch (error) {
      console.error('PDF  :', error);
      alert('PDF     .     .');
      return null;
    }
  };

  return (
    <div className="relative group">
        <input
          ref={fileInputRef}
          type="file"
          accept=".txt,.pdf"
          onChange={handleFileSelect}
          className="hidden"
          disabled={disabled || isProcessing}
          multiple
        />
      
      <button
        onClick={() => fileInputRef.current?.click()}
        disabled={disabled || isProcessing}
        className={`p-2 rounded-lg transition-all duration-200 flex items-center justify-center ${
          disabled || isProcessing
            ? 'bg-bg-200 text-text-400 cursor-not-allowed'
            : 'bg-bg-200 hover:bg-bg-300 text-text-300 hover:text-text-100 hover:shadow-sm'
        }`}
        title={isProcessing ? '  ...' : '  (txt, pdf)'}
        style={{
          backgroundColor: disabled || isProcessing ? 'hsl(var(--bg-200))' : 'hsl(var(--bg-200))',
          color: disabled || isProcessing ? 'hsl(var(--text-400))' : 'hsl(var(--text-300))'
        }}
      >
        {isProcessing ? (
          <div className="animate-spin h-5 w-5 border-2 border-text-400 border-t-transparent rounded-full" />
        ) : (
          <PaperClipIcon className="h-5 w-5" />
        )}
      </button>


      {/*     */}
      {isProcessing && (
        <div className="absolute bottom-full left-0 mb-2 px-2 py-1 text-white text-xs rounded whitespace-nowrap animate-pulse z-50"
             style={{ backgroundColor: 'hsl(var(--accent-main-100))' }}>
            ...
        </div>
      )}
    </div>
  );
});

FileUploadButton.displayName = 'FileUploadButton';

export default FileUploadButton;